<template>
    <div class="container">
        <div class="main">
            <div ref="pdf" class="mian">
                <pdf :src="url" v-for="i in numPages" :key="i" :page="i"> </pdf>
                <div class="demo">
                    <img :src="imgSrc" class="img" v-show="imgSrc" ref="img" />
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import pdf from "vue-pdf";
export default {
    data() {
        return {
            url: "",
            numPages: 1,
            imgSrc: "",
        };
    },
    components: {
        pdf,
    },
    mounted() {
        document.documentElement.scrollTop = 0;
        setTimeout(() => {
            if (!this.$store.state.evPdf) return;
            let file = this.$store.state.evPdf;
            let fr = new FileReader();
            fr.readAsDataURL(file);
            fr.onload = (pdf) => {
                this.getNumPages(pdf.target.result);
            };
        })
    },
    methods: {
        getNumPages(url) {
            let loadingTask = pdf.createLoadingTask(url);
            loadingTask.promise
                .then((Pdf) => {
                    this.url = loadingTask;
                    console.log("loadingTask:", loadingTask);
                    this.numPages = Pdf.numPages;
                    this.imgSrc = this.$store.state.img;
                    this.$nextTick(() => {
                        let el = document.querySelector(".img");
                        el.style = this.$store.state.style;

                        // 全部数据
                        // console.log(JSON.stringify({
                        //     pdf: this.$store.state.pdf,
                        //     img: this.$store.state.img,
                        //     matrix: this.$store.state.matrix,
                        //     style: this.$store.state.style,
                        // }));
                    })
                })
                .catch(() => {
                    console.error("pdf加载失败");
                });
        },
    },
};
</script>
<style scoped>
.container {
    font-family: PingFang SC;
    width: 100%;
    overflow: hidden;
}

.demo {
    position: absolute;
    bottom: 100px;
    right: 100px;
}

.img {
    /* position: absolute; */
    width: 250px;
    height: 100px;
}

.mian {
    width: 100%;
    position: relative;
}
</style>



