import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);


// vue-pdf  
// hammerjs
export default new Vuex.Store({
  state: {
    pdf: null,   //pdf base64
    evPdf: null,
    img: null,   //img base64
    evImg: null,
    matrix: null, // 矩阵
    style: '', //样式
  },
  mutations: {
    pdfChange(state, data) {
      state.pdf = data
    },
    evPdfChange(state, data) {
      state.evPdf = data
    },
    imgChange(state, data) {
      state.img = data
    },
    evImgChange(state, data) {
      state.evImg = data
    },
    matrixChange(state, data) {
      state.matrix = data
    },
    styleChange(state, data) {
      state.style = data
    },
    clear(state) {
      state.pdf = null
      state.evPdf = null
      state.img = null
      state.evImg = null
      state.matrix = null
      state.style = ''
    },
  }
})



