import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import Preview from '@/components/preview'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/preview',
      name: 'Preview',
      component: Preview
    },
  ]
})



