<template>
  <div class="container">
    <div class="main">
      <div ref="pdf" class="mian">
        <pdf :src="url" v-for="i in numPages" :key="i" :page="i"> </pdf>
        <div class="demo">
          <img :src="imgSrc" class="img" v-show="imgSrc" ref="img" />
        </div>
      </div>
      <div v-if="!url">
        上传PDF:<input type="file" id="files" @change="fileChange" accept="application/pdf" value="上传PDF" />
      </div>
      <!-- capture="camera" -->
      <div v-if="!imgSrc">
        上传签名:<input type="file" accept="image/*" @change="selectFile" value="上传签名" />
      </div>
    </div>
    <div style="padding:100px 0">
      <button @click="getDatas">预览</button>
    </div>
  </div>
</template>

<script>
import pdf from "vue-pdf";
import Hammer from "hammerjs";
export default {
  data() {
    return {
      url: "",
      numPages: 1,
      imgSrc: "",
      config: {},
      id: null,
      mc: null,
      translateX: 0,
      translateY: 0,
      scale: 1,
      firstTouch: true,
      relateX: 0,
      relateY: 0,
      oldX: 0,
      oldY: 0,
      oldScale: 1,
    };
  },
  components: {
    pdf,
  },
  methods: {
    getDatas() {
      let preview = window.confirm('预览')
      if (preview && this.$store.state.pdf) {
        this.$router.push('/preview')
      }
      var el = document.querySelector(".img");;
      var st = window.getComputedStyle(el, null);
      var tr = st.getPropertyValue("-webkit-transform") ||
        st.getPropertyValue("-moz-transform") ||
        st.getPropertyValue("-ms-transform") ||
        st.getPropertyValue("-o-transform") ||
        st.getPropertyValue("transform") ||
        "FAIL";
      var values = tr.split('(')[1].split(')')[0].split(',');
      var a = values[0];
      var b = values[1];
      var scale = Math.sqrt(a * a + b * b)
      let style = `touch-action: none; user-select: none; -webkit-user-drag: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); transform: translate3d(${values[4]}px, ${values[5]}px, 0px) scale(${scale}, ${scale});`
      this.$store.commit('matrixChange', values);
      this.$store.commit('styleChange', style);

    },
    selectFile(event) {
      if (event.target.files && event.target.files.length > 0) {
        let file = event.target.files[0];
        let fr = new FileReader();
        fr.readAsDataURL(file);
        fr.onload = (imgObj) => {
          let img = new Image();
          img.src = imgObj.target.result;
          img.onload = (e) => {
            // 这里就可以获取到上传的图片了
            this.imgSrc = imgObj.target.result;
            this.$store.commit('imgChange', imgObj.target.result)
            this.$nextTick(() => {
              this.init();
            });
          };
        };
      }
    },
    fileChange(ev) {
      //文件上传到阿里云oss获得url
      let url = "";
      let file = ev.target.files[0];
      this.$store.commit('evPdfChange', file)
      let fr = new FileReader();
      fr.readAsDataURL(file);
      fr.onload = (pdf) => {
        url = pdf.target.result;
        this.$store.commit('pdfChange', pdf.target.result)
        this.getNumPages(url);
      };
      // this._upload(ev, (url) => {
      //   url = url
      // })
      // url = "/static/git.pdf";
      // this.getNumPages(url);
    },
    getNumPages(url) {
      let loadingTask = pdf.createLoadingTask(url);
      loadingTask.promise
        .then((Pdf) => {
          this.url = loadingTask;
          this.numPages = Pdf.numPages;
        })
        .catch(() => {
          console.error("pdf加载失败");
        });
    },

    init() {
      this.picInit();
    },
    picInit() {
      this.id = document.querySelector(".img");
      this.mc = new Hammer(this.id);
      // this.relateX = (document.body.clientWidth - this.id.offsetWidth) / 2;
      // this.relateY = (document.body.clientHeight - this.id.offsetHeight) / 2;
      this.relateX = 0;
      this.relateY = 0;
      this.mc.add(
        new Hammer.Pan({
          direction: Hammer.DIRECTION_ALL,
          threshold: 0,
          pointers: 0,
        })
      );
      this.mc
        .add(
          new Hammer.Pinch({
            threshold: 0,
          })
        )
        .recognizeWith(this.mc.get("pan"));

      this.mc.on("hammer.input", this.isFinal);
      this.mc.on("panstart panmove", this.onPan);
      this.mc.on("pinchstart pinchmove", this.onPinch);

      this.setPosition();
    },
    isFinal(ev) {
      if (ev.isFinal) {
        this.oldX = this.translateX;
        this.oldY = this.translateY;
        this.oldScale = this.scale;
      }
    },
    // 初始化图片位置及缩放
    setPosition() {
      this.selfPosition({
        translateX: this.relateX,
        translateY: this.relateY,
        scale: this.scale,
      });
    },
    // 单点触发 - 拖拉
    onPan(ev) {
      // console.log(this.firstTouch)
      if (this.firstTouch) {
        this.oldX = this.relateX;
        this.oldY = this.relateY;
      }
      // console.log(this.oldX)
      // console.log(this.oldY)
      this.translateX = this.oldX + ev.deltaX;
      this.translateY = this.oldY + ev.deltaY;
      const position = {
        translateX: this.translateX,
        translateY: this.translateY,
        scale: this.scale,
      };
      this.selfPosition(position);
      this.firstTouch = false;
    },
    // 多点触发 - 缩放
    onPinch(ev) {
      this.scale = this.oldScale * ev.scale;
      this.selfPosition({
        translateX: this.translateX,
        translateY: this.translateY,
        scale: this.scale,
      });
      // this.selfPosition(this.position)
    },
    selfPosition(pos) {
      return this.picAnimate()(() => this.tempPos(pos));
    },
    tempPos(pos) {
      let style = [
        `translate3d(${pos.translateX}px, ${pos.translateY}px, 0)`,
        `scale(${pos.scale}, ${pos.scale})`,
        // `scale(${pos.scale > 1.2 ? 1.2 : pos.scale}, ${pos.scale > 1.2 ? 1.2 : pos.scale})`
      ];
      style = style.join(" ");
      this.id.style.transform = style;
    },
    picAnimate() {
      return (
        window[Hammer.prefixed(window, "requestAnimationFrame")] ||
        function (callback) {
          setTimeout(callback, 1000 / 60);
        }
      );
    },
  },
};
</script>

<style scoped>
.container {
  font-family: PingFang SC;
  width: 100%;
  overflow: hidden;
}

.demo {
  position: absolute;
  bottom: 100px;
  right: 100px;
}

.img {
  /* position: absolute; */
  width: 250px;
  height: 100px;
}

.mian {
  width: 100%;
  position: relative;
}
</style>




